@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{display:flex;justify-content:center;align-items:center}.center{width:180px;height:60px;position:absolute}.btn{width:180px;height:60px;cursor:pointer;background:transparent;border:2px solid #91C9FF;border-width:2px;outline:none;transition:1s ease-in-out;display:flex;justify-content:center;align-items:center}svg{position:absolute;left:0;top:0;fill:none;stroke:#fff;stroke-dasharray:150 480;stroke-dashoffset:150;transition:1s ease-in-out}.btn:hover{transition:1s ease-in-out}.btn:hover svg{stroke-dashoffset:-480}.btn span{color:white;display:block}

body{font-family:'Open Sans', sans-serif;background-color:#1e1e1e}*{box-sizing:border-box}html{scroll-behavior:smooth}ul{list-style-type:none;padding:0}button,.button{border:2px solid;padding:13px 30px;background-color:transparent;color:white;font-weight:600;font-size:16px;text-decoration:none;cursor:pointer}button.button-yellow,.button.button-yellow{border-color:#faaf40}button.button-borderless,.button.button-borderless{border-color:transparent}body{font-family:'Open Sans', sans-serif}p{font-size:16px}*{box-sizing:border-box}h1,h2,h3,h4,h5,h6{letter-spacing:2px}ul{list-style-type:none;padding:0}button,.button{border:2px solid;padding:13px 30px;background-color:transparent;color:white;font-weight:600;font-size:16px;letter-spacing:2px;text-decoration:none}button.button-yellow,.button.button-yellow{border-color:#faaf40}button.button-borderless,.button.button-borderless{border-color:transparent}.sectionTitle{font-weight:700;font-size:16px}section{background-color:#1e1e1e;min-height:100vh;min-width:176px;padding:50px 30px;text-align:center;font-size:calc(10px + 2vmin);color:white}@media only screen and (min-width: 768px){section{padding-bottom:80px}}.hero{display:flex;position:relative;flex-direction:column;align-items:center;justify-content:space-between}.titles{margin:0;color:#cecece;font-weight:600;letter-spacing:2px}.logo{max-width:70vw}.my-work{background-color:#faaf40;display:flex;flex-direction:column;justify-content:center;position:relative;background-position:top center;background-size:cover}.my-work a{font-size:40px;font-weight:800;color:white;opacity:.4;text-decoration:none}@media only screen and (min-width: 768px){.my-work a{font-size:90px}}.my-work a:hover{opacity:1}.my-work .buttonContainer{position:absolute;bottom:50px;left:50%;transform:translateX(-50%)}@media only screen and (min-width: 768px){.my-work .buttonContainer{bottom:80px}}.my-work.disney{background-image:url("/img/disney/background.png")}.my-work.bby{background-image:url("/img/bby/background.jpg")}.my-work.dh{background-image:url("/img/dh/background.png")}.my-work.misc{background-image:url("/img/misc/header - miscellaneous@2x.png")}.about-me h2{color:#faaf40}.about-me p{max-width:600px;margin:20px auto}.about-me a.download{width:300px}.contactLinks{margin-top:100px}.contactContainer{position:relative;height:60px}.profile-pic{max-width:420px;width:90%;margin-bottom:20px}

body{font-family:'Open Sans', sans-serif;background-color:#1e1e1e}*{box-sizing:border-box}html{scroll-behavior:smooth}ul{list-style-type:none;padding:0}button,.button{border:2px solid;padding:13px 30px;background-color:transparent;color:white;font-weight:600;font-size:16px;text-decoration:none;cursor:pointer}button.button-yellow,.button.button-yellow{border-color:#faaf40}button.button-borderless,.button.button-borderless{border-color:transparent}.CaseStudy{text-align:center;background-color:#1e1e1e;color:white}.CaseStudy__hero{height:480px;background-position:top center;background-size:cover}.CaseStudy__hero h1{letter-spacing:0;font-size:36px;font-weight:800;padding-top:180px;margin-bottom:10px;margin-top:0}@media only screen and (min-width: 768px){.CaseStudy__hero h1{font-size:100px}}.CaseStudy__hero h2{font-size:16px;font-weight:700;margin-top:0}@media only screen and (min-width: 768px){.CaseStudy__hero{height:700px}}.CaseStudy__hero-disney{background-position:bottom right}.CaseStudy__hero,.CaseStudy__content{padding:0 44px;position:relative}.CaseStudy__back{padding:24px 0 0;border:none;float:left;text-align:left;color:white;text-decoration:none;font-weight:700;letter-spacing:2px}.CaseStudy__device{position:absolute;display:block;top:0;left:50%;transform:translate(-50%, -50%);width:90%;max-width:800px}.CaseStudy__device-bby{max-width:600px}.CaseStudy__device-misc{width:120px}@media only screen and (min-width: 768px){.CaseStudy__device-misc{width:250px}}.CaseStudy__summary{color:#faaf40;letter-spacing:2;font-size:16px;font-weight:600;padding-top:180px}@media only screen and (min-width: 768px){.CaseStudy__summary{padding-top:380px}}.CaseStudy__summary-dh{padding-top:120px}@media only screen and (min-width: 768px){.CaseStudy__summary-dh{padding-top:240px}}.CaseStudy p{max-width:600px;margin-left:auto;margin-right:auto}.CaseStudy__stats{display:flex;flex-wrap:wrap;justify-content:space-between;margin:80px auto 0;max-width:700px}.CaseStudyStat{text-align:left;width:150